import MyInput from '@/components/MyInput/MyInput.vue'
import MyForm from '../../components/MyForm/MyForm.vue'
import MyFormItem from '../../components/MyForm/MyFormItem.vue'
export default {
	components: {
		MyInput,
		MyForm,
		MyFormItem
	},
	data() {
		return {
			model: {
				mail: '',
				pass: '',
				username: '',
				checkPass: ''
			},
			rules: {
				mail: [{
						reg: /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/,
						mode: 'input',
						message: '请输入正确邮箱'
					},
					{
						required: true,
						message: '邮箱不能为空',
						mode: 'blur'
					},
				],
				username: [{
						required: true,
						message: '昵称不能为空',
						mode: 'blur'
					},
					{
						reg: /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/,
						message: '昵称为2至12位中文,字母,数字',
						mode: 'input'
					}
				],
				pass: [{
						required: true,
						message: '密码不能为空',
						mode: 'blur'
					},
					{
						reg: /^[a-zA-Z]{1}([a-zA-Z0-9]|[._]){6,20}$/,
						mode: 'input',
						message: '以字母开头,不能有._以外的符号6-20位'
					}
				],
				checkPass: [{
						required: true,
						message: '请再次输入密码',
						mode: 'blur'
					},
					{
						reg: /^[a-zA-Z]{1}([a-zA-Z0-9]|[._]){6,20}$/,
						mode: 'input',
						message: '以字母开头,不能有._以外的符号6-20位'
					}
				]
			}
		}
	},
	props: {
		type: {
			type: String,
			default: 'default'
		},
		inverted: {
			type: Boolean,
			default: false
		},
		text: {
			type: [String, Number],
			default: ''
		},
		size: {
			type: String,
			default: 'normal'
		}
	},
	computed: {},
	methods: {
		async submit() {
			if (this.model.pass !== this.model.checkPass) {
				uni.showToast({
					title: '两次输入密码不一致',
					icon: 'none'
				})
				return
			}
			let res = await this.$post('/api/reg', this.model)
			let {data} = res
			if (data.status === 1) {
				uni.showToast({
					title: data.data.masg
				})
				uni.redirectTo({
					url: '../login/login'
				})
			} else {
				uni.showToast({
					title: data.data.msg,
					icon: 'none'
				})
			}
		}
	}
}
